customElements.define(
    'horarios-page',
    class extends HTMLElement {
        shadow = this.attachShadow({ mode: 'open' });
        constructor() {
            super();
            this.render();
        }
        render() {
            const div = document.createElement("div")
            div.innerHTML = `
            <h1>horarios</h1>
            <p>lunes a viernes de 9 a 18hs.</p>
            `
            this.shadow.appendChild(div)
        }
    });